.global-box{
    background-color: rgb(51, 64, 75) !important;
    width: 100%;
    height: 300px;
    margin-bottom: 30px;
    padding: 20px !important;

}
.bar-diagram{
    width: 460px !important;
    height: 240px !important;
}
.dot{
    background-color: #fff;
    border-radius: 50%;
    width: 3px;
    height: 3px;
    margin-bottom: 2px;

}
.box-display{
    display: none;
}
.dialog-3-box{
    background-color: rgb(42, 49, 56);
    color: indianred;
    box-shadow: 1px -1px 30px rgba(154,161,171,.15) !important ;
    -webkit-box-shadow: 1px -1px 30px rgba(154,161,171,.15) !important ;
    width: 100px;
    border-radius: 5px !important;
    cursor: pointer;
    position: absolute;
    z-index: 1000;
    transform: translate(-60%,-15%);

}
.classes-3{
    padding: 5px;
}
.top-states{
    color:#fff8;
    font-size:20px;
    margin-top:30px;
    line-height: 1rem;
}
@media (max-width: 575.98px) {
    .bar-diagram{
        width: 260px !important;
        height: 240px !important;
    }

  }


@media (min-width: 576px) and (max-width: 767.98px) { 
    .bar-diagram{
        width: 300px !important;
        height: 240px !important;
    }
 }


@media (min-width: 768px) and (max-width: 991.98px) { 
    .bar-diagram{
        width: 560px !important;
        height: 240px !important;
    }
}


@media (min-width: 992px) and (max-width: 1199.98px) { 
    .bar-diagram{
        width: 350px !important;
        height: 240px !important;
    }
 }
