.navbar{
    background-color: none !important;
    min-height: 70px;
    padding: 5px 30px !important;
    box-shadow: 1px -1px 30px rgba(154,161,171,.15) !important ;
    -webkit-box-shadow: 1px -1px 30px rgba(154,161,171,.15) !important ;
   
}
.deep-margin{
    margin-top: 10px !important;
}
.deep-name{
    line-height: 0.7rem;
    margin-left: 10px;
}
.state-name{
    width: 490px !important;
    background-color: rgb(42, 49, 56) !important;
    color: indianred;
    border:none !important;
    outline: none !important;
    border-bottom:1.4px solid #fff !important;
}
.search-box{
    display: none;
    margin-left: 150px;
}
@media (max-width: 375px) {
    .state-name{
        width:210px !important;
        margin-top: 10px !important;
        background-color:rgb(51, 64, 75) !important ;
      }
      .toggle-pad{
        display: block;
    }
    .navbar{
        background-color: rgb(51, 64, 75) !important;
    }
    .deep-name{
      display: none;  
    }
    .deep-margin{
        margin-top: 3px !important;
    }
}
@media (min-width: 375px) and (max-width: 450px) {
    .state-name{
        width:260px !important;
        margin-top: 10px !important;
        background-color:rgb(51, 64, 75) !important ;
      }
      .toggle-pad{
        display: block;
    }
    .navbar{
        background-color: rgb(51, 64, 75) !important;
    }
    .deep-name{
      display: none;  
    }
    .deep-margin{
        margin-top: 3px !important;
    }
}
@media (min-width: 450px) and (max-width: 575.98px) {
    .toggle-pad{
        display: block;
    }
    .navbar{
        background-color: rgb(51, 64, 75) !important;
    }
    .deep-name{
      display: none;  
    }
    .state-name{
      width:260px !important;
      margin-top: 10px !important;
      background-color:rgb(51, 64, 75) !important ;
    }

    .deep-margin{
        margin-top: 3px !important;
    }
  }


@media (min-width: 576px) and (max-width: 767.98px) { 
    .navbar{
        background-color: rgb(51, 64, 75) !important;
    }
    .deep-name{
        display: none;  
     }
     .state-name{
        width:350px !important;
        margin-top: 10px !important;
        background-color:rgb(51, 64, 75) !important ;
      }
    .deep-margin{
        margin-top: 3px !important;
    }
 }


@media (min-width: 768px) and (max-width: 991.98px) { 
    .navbar{
        background-color: rgb(51, 64, 75) !important;
    }
    .state-name{
        width:470px !important;
        margin-top: 10px !important;
        background-color:rgb(51, 64, 75) !important ;
      }
    .deep-margin{
        margin-top: 3px !important;
    }
}


@media (min-width: 992px) and (max-width: 1199.98px) { 
    .toggle-pad{
        display: block !important;
    }
    .navbar{
        background-color: rgb(51, 64, 75) !important;
    }

    .deep-margin{
        margin-top: 7px !important;
    }
    .state-name{
        background-color:rgb(51, 64, 75) !important ;
    }
 }
